import React, {Component} from 'react'
import './Planets.scss'
import Loader from './Loader'
import {connect} from 'react-redux'
import {FETCHLIMIT, TIMERLIMIT} from '../config/constants'


class Planets extends Component {
  constructor(props) {
    super(props)
    this.debounce = null
    this.apiFetchCount = 1
  }
  
  startTimer = () => {
    if(!this.timerController) {
      this.timerController = setInterval(() => {
        if(this.props.timer === 1) {
            this.stopTimer()
            this.apiFetchCount = 1
        } else {
            this.props.decrementTimer()
        }
      }, 1000)
    }
  }

  stopTimer = () => {
    if(this.timerController) {
      clearInterval(this.timerController)
      this.props.resetTimer()
      delete(this.timerController)
    }
  }

  handleTypingSearch = (el) => {
    let value = el.target.value
    this.props.setCharacter({[el.target.name]: value})
    this.props.setSearchResult({searchResult: []})
    this.debounce = new Date().getTime()
    let debouncedelay = 500
    setTimeout(() => {
      let debouncediff = new Date().getTime() - this.debounce
      if(debouncediff >= debouncedelay) {
        if(value.trim() !== "") {
          if(this.props.username === "Luke Skywalker") {
            this.props.setLoading({loading: true})
            fetch(`https://swapi.co/api/planets/?search=${this.props.search}`, {mode: "cors"})
              .then(result => result.json())
              .then(data => {
                this.props.setState({searchResult: data, loading: false})
              })
              .catch(() => {
                console.log('Unable to Connect!')
                this.props.setLoading({loading: false})
            })

          } 
          else {
            if(this.apiFetchCount <= FETCHLIMIT) {
              this.props.setLoading({loading: true})
              this.startTimer()
              fetch(`https://swapi.co/api/planets/?search=${this.props.search}`, {mode: "cors"})
                .then(result => result.json())
                .then(data => {
                  this.props.setState({searchResult: data, loading: false})
                  this.apiFetchCount++
                })
                .catch(() => {
                  console.log('Unable to Connect!')
                  this.props.setLoading({loading: false})
              })
              if(this.apiFetchCount === FETCHLIMIT) {
                console.log('TIMEOUT!')
                this.props.setState({wait: true})
              }
            } 
          }
        } 
        else {
          this.props.setSearchResult({searchResult: []})
        }
      }
    }, debouncedelay)
  }

  render() {
    const searchResult = this.props.searchResult && this.props.searchResult.results && this.props.searchResult.results.map((planet,key) => {
    let dimension
    if(planet.population === "unknown") {
        dimension = 800
    } else if(planet.population >= 1000000000000) {
        dimension = 700
    } else if(planet.population >= 10000000000 && planet.population < 1000000000000) {
        dimension = 600
    } else if(planet.population >= 100000000 && planet.population < 10000000000) {
        dimension = 500
    } else if(planet.population >= 1000000 && planet.population < 100000000) {
        dimension = 400
    } else if(planet.population >= 10000 && planet.population < 1000000) {
        dimension = 300
    } else if(planet.population >= 100 && planet.population < 10000) {
        dimension = 200
    } else {
        dimension = 100 
    }

    const hue = dimension / 800 * 360

    const style = {
      width: dimension,
      height: dimension,
      fontSize: dimension/10,
      boxShadow: `0 0 5px 1px hsla(${hue},100%,50%,0.5), inset 0 0 50px 1px hsla(${hue},100%,50%,0.8)`
    }
    return <li key={key} style={style}><span style={{color: "red"}}>{planet.name}</span><span style={{color: "blue"}}>{planet.population === "unknown" ? null : planet.population }</span></li>
    })

    return (
      <>
        <div className="Planets">
            <div className="Logout">
              <button onClick={this.props.logout}>Logout</button>
            </div>
            <div className="User">
              <div>Welcome <strong>{this.props.username}</strong>!</div>
              <div className="Timer">{this.props.timer === TIMERLIMIT ? null : this.props.timer}</div>
              <div className="Wait">{this.props.wait ? "Fetch Limit Exceeded! Please wait..." : null}</div>
            </div>
            <div className="BackwardForward">
              <button className={this.props.bFinished} onClick={this.props.backward}>Back</button>
              <button className={this.props.fFinished} onClick={this.props.forward}>Next</button>
            </div>
            <div className="Search">
              <input name="search" placeholder="Search" onChange={this.handleTypingSearch}/>
            </div>
            <ul>{this.props.loading ? <Loader/> : searchResult}</ul>
        </div>
      </>
    )
  }

  componentWillUnmount() {
    this.stopTimer()
  }
}



const decrementTimer = () => {
  return {type: "DECREMENT_TIMER"}
}



const resetTimer = () => {
  return {type: "RESET_TIMER"}
}



const setCharacter = (character) => {
  return {type: "UPDATE_STATE", payload: {...character}}
}



const setSearchResult = (searchResult) => {
  return {type: "UPDATE_STATE", payload: {...searchResult}}
}



const setLoading = (loading) => {
  return {type: "UPDATE_STATE", payload: {...loading}}
}



const setState = (data) => {
  return {type: "UPDATE_STATE", payload: {...data}}
}



const backward = () => (dispatch, getState) => {
  dispatch({type: "UPDATE_STATE", payload: {bFinished: ""}})
  const {planets} = getState()
  if(planets.searchResult && planets.searchResult.previous) {
    const previous = planets.searchResult.previous
    dispatch({type: "UPDATE_STATE", payload: {searchResult: [], loading: true}})
    fetch(previous, {mode: "cors"})
      .then(result => result.json())
      .then(data => {
        dispatch({type: "UPDATE_STATE", payload: {searchResult: data, loading: false}})
    })
    
  } else {
      setTimeout(() => {dispatch({type: "UPDATE_STATE", payload: {bFinished: "Finished"}})}, 0)
  }
}



const forward = () => (dispatch, getState) => {
  dispatch({type: "UPDATE_STATE", payload: {fFinished: ""}})
  const {planets} = getState()
  if(planets.searchResult && planets.searchResult.next) {
    const next = planets.searchResult.next
    dispatch({type: "UPDATE_STATE", payload: {searchResult: [], loading: true}})
    fetch(next, {mode: "cors"})
      .then(result => result.json())
      .then(data => {
        dispatch({type: "UPDATE_STATE", payload: {searchResult: data, loading: false}})
    })
  } else {
      setTimeout(() => {dispatch({type: "UPDATE_STATE", payload: {fFinished: "Finished"}})}, 0)
  }
}



const mapStateToProps = (state) => {
  return {
    search: state.planets.search,
    searchResult: state.planets.searchResult,
    bFinished: state.planets.bFinished,
    fFinished: state.planets.fFinished,
    loading: state.planets.loading,
    timer: state.planets.timer,
    wait: state.planets.wait,
    state: state.planets
  }
}



const mapDispatchToProps = {
  forward: forward,
  backward: backward,
  decrementTimer: decrementTimer,
  resetTimer: resetTimer,
  setCharacter: setCharacter,
  setSearchResult: setSearchResult,
  setLoading: setLoading,
  setState: setState
}



export default connect(mapStateToProps, mapDispatchToProps)(Planets)
