import React, {Component} from 'react';
import './Login.scss';
import Planets from './Planets';
import Loader from './Loader'
import {connect} from 'react-redux'

class Login extends Component {
  render() {
    const loginErrorStatus = this.props.loginError ? "Login LoginError" : "Login"

    const Loading = {marginTop: "20px", marginBottom: "20px"}
    const NotLoading = {marginTop: "0", marginBottom: "0"}
    const LoaderStyle = this.props.loading ? Loading : NotLoading

    const loginForm = <div className="LoginContainer">
                        <div className={loginErrorStatus}>
                            <form onSubmit={this.props.handleLogin}>
                                <input name="username" onInput={this.props.handleInput} placeholder="Username"/>
                                <input type="password" name="password" onInput={this.props.handleInput} placeholder="Password"/>
                                <button>Login</button>
                            </form>
                            <div className="Loader" style={LoaderStyle}>{this.props.loading ? <Loader/> : null}</div>
                        </div>
                      </div>
    return (
        <>
            {this.props.loggedIn ? <Planets username={this.props.username} logout={this.props.handleLogout}/> : loginForm }
        </>
    );
  }
}

const handleLogin = (e) => (dispatch, getState) => {
  e.preventDefault()
  dispatch({type: "UPDATE_STATE", payload: {loginError: false, loading: true}})
  fetch("https://swapi.co/api/people/", {mode: "cors"})
      .then(result => result.json())
      .then(data => {
        dispatch({type: "UPDATE_STATE", payload: {people: data.results}})
        const {login, planets} = getState()
        let found = planets.people.find(character => {
          return character.name === login.username && character.birth_year === login.password
        })
        if(found)
          dispatch({type: "UPDATE_STATE", payload: {loggedIn: true, loading: false}})
        else
          dispatch({type: "UPDATE_STATE", payload: {loggedIn: false, loading: false, loginError: true}})
      })
      .catch(() => {
        console.log('Unable to Connect!')
        dispatch({type: "UPDATE_STATE", payload: {loading: false}})
      })
}

const handleInput = (el) => (dispatch) => {
  dispatch({type: "UPDATE_STATE", payload: {[el.target.name]: el.target.value}})
}

const handleLogout = () => (dispatch) => {
  dispatch({type: "UPDATE_STATE", payload: {loggedIn: false, username: "", password: ""}})
  console.log('Logged Out!')
}

const mapStateToProps = (state) => {
  return {
    loginError: state.login.loginError,
    loggedIn: state.login.loggedIn,
    loading: state.login.loading,
    people: state.login.people,
    password: state.login.password,
    username: state.login.username
  }
}

const mapDispatchToProps = {
  handleLogin: handleLogin,
  handleInput: handleInput,
  handleLogout: handleLogout
}

export default connect(mapStateToProps, mapDispatchToProps)(Login)
