// https://github.com/vipinkrishna

import * as types from '../actions/action-types'

import {TIMERLIMIT} from '../../config/constants'

const initialState = {
    search: null,
    searchResult: null,
    bFinished: "",
    fFinished: "",
    loading: false,
    timer: TIMERLIMIT,
    wait: false
}
export default function (state=initialState, {type, payload}) {
    const newState = {...state}
    switch(type) {
        case types.UPDATE_STATE:
            return {...state, ...payload}
        case types.DECREMENT_TIMER:
            const {timer} = state
            return {...state, timer: timer-1}
        case types.RESET_TIMER:
            return {...state, timer: TIMERLIMIT, wait: false}
        default:
            return newState
    }
}